.gamezone-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-image: url('../img/seamless3.jpg');
  background-size: 65%;
  padding-bottom: 75px;
}

.gamezone-title {
  width: 100%;
  text-align: center;
  color: white;
  font-family: 'Righteous', cursive;
  font-size: 3rem;
}

.game-container {
  width: 80%;
  height: 800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.49);
  backdrop-filter: blur(8.600000381469727px);
  font-family: 'Righteous', cursive;
}

.game {
  width: 100%;
  height: 600px;
}

.game-description-container {
  min-height: 200px;
  height: 20%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 0px 15px;
}

.project-selector {
  width: 70%;
  height: 100px;
  border: 2px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 25px;
  box-shadow: 0px 5px 5px 0.3px rgba(0, 0, 0, 0.329);
  margin-bottom: 50px;
}

.preview-container {
  height: 100%;
  width: 20%;
  cursor: pointer;
  transition: all ease-in-out 0.3s;
  border: 2px solid black;
}

.preview-container:hover {
  transform: scale(1.1);
}

.preview-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

@media (min-width: 850px) {
  .game-container {
    flex-direction: row;
  }

  .game {
    width: 70%;
    height: 100%;
  }

  .game-description-container {
    height: 100%;
    width: 30%;
  }
}
